@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Alfa+Slab+One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth;
}

.loading-screen {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}


.container,
body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.navItemWrapper {
  position: absolute;
  display: flex;
  justify-content: flex-end;
  padding-right: 15%;
  width: 100%;
}

.navItemContainer {
  color: #232323 !important;
  padding: 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.5 !important;
  transition: all ease-out 0.2s !important;
  height: 45px;
}
.navItemContainer:hover {
  opacity: 1 !important;
}
.navItemContainer.active {
  opacity: 1 !important;
  color: black !important;
}

.main-content {
  flex: 1 1;
}
footer {
  text-align: center;
  padding: 3px;
  background-color: #292c2f;
  color: white;
  bottom: 0px;
  width: 100%;
  height: 10vh;
  align-self: flex-end;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
}
.logo-store {
  width: 125px;
}

.navbar {
  display: flex;
  align-items: center;
  padding: 20px;
}

nav {
  flex: 1 1;
  text-align: right;
}

.nav {
  flex: 1 1;
  text-align: right;
}

.nav ul {
  display: inline-block;
  list-style-type: none;
}

nav ul {
  display: inline-block;
  list-style-type: none;
}

nav ul li {
  display: inline-block;
  margin-right: 20px;
}

a {
  text-decoration: none;
  color: #555;
}

p {
  color: #555;
}

.homepage {
  max-width: 1300px;
  margin: auto;
  padding-left: 25px;
  padding-right: 25px;
}

.row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
}

.col-2 {
  flex-basis: 50%;
  min-width: 300px;
}

.col-2 img {
  max-width: 100%;
  padding: 50px 0;
}

.col-2 h1 {
  font-size: 50px;
  line-height: 60px;
  margin: 25px 0;
}

.btn {
  display: inline-block;
  vertical-align: middle;
  background: #ff523b;
  color: #fff;
  padding: 8px 30px;
  margin: 30px 0;
  border-radius: 30px;
  border-width: 0px;
  cursor: pointer;
}

.btn:hover {
  background: #563434;
}

.home-container {
  height: 100vh;
  background: radial-gradient(#fff, #ffd6d6);
}

.home-container .row {
  margin-top: 0px;
}

.column-latest {
  display: grid;
  grid-template-columns: auto auto auto;
  justify-items: center;
  grid-row-gap: 50px;
  padding-top: 5%;
  padding-bottom: 5%;
  min-height: 90vh;
}

.column-latest-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 4px 18px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 15px;
  height: 500px;
  min-width: 55%;
  z-index: 2;
  padding: 0px;
  margin: 0px;
}

.column-latest-img {
  background-image: cover;
  width: 280px !important;
  height: 200px !important;
  border-radius: 15px 15px 0px 0px;
}

.column-latest-1 {
  padding-top: 8%;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
}

.column-latest-2 {
  font-size: 16px;
