}

.column-variant {
  width: 100%;
  display: grid;
  grid-template-columns: auto auto auto auto;
  justify-items: center;
  align-items: center;
  grid-row-gap: 50px;
  padding-top: 1%;
  padding-bottom: 1%;
  min-height: 30vh;
}

.column-variant-rasa {
  display: flex;
  justify-content: space-between;
  align-items: center;
  justify-content: center;
  margin: 4px auto;
  color: #fff;
  background: linear-gradient(
    90deg,
    rgba(255, 118, 20, 1) 0%,
    rgba(255, 84, 17, 1) 100%
  );

  padding: 16px;
  border-radius: 5px;
  width: 90%;
}

.column-variant-rasa:nth-child(4n + 1) {
  background: linear-gradient(
    90deg,
    rgb(181, 149, 247) 0%,
    rgba(155, 0, 250, 1) 100%
  );
}

.column-variant-rasa:nth-child(4n + 2) {
  background: linear-gradient(
    90deg,
    rgba(255, 12, 241, 1) 0%,
    rgba(250, 0, 135, 1) 100%
  );
}

.column-variant-rasa:nth-child(4n + 3) {
  background: linear-gradient(
    90deg,
    rgba(20, 159, 255, 1) 0%,
    rgba(17, 122, 255, 1) 100%
  );
}

